.heading {
  margin: 2rem 0 5rem 0;
}

.primary_text,
.heading,
.final_score {
  text-align: center;
}

.play_again_btn {
  display: block;
  margin: 5rem auto 0 auto;
  outline: 0;
  cursor: pointer;
  font-size: 1rem;
  font-weight: bold;
  padding: 10px 1rem;
  letter-spacing: 2px;
  background-color: black;
  color: white;
}

@media screen and (max-width: 960px) {
  .play_again_btn {
    display: block;
    margin: 5rem auto 0 auto;
    outline: 0;
    cursor: pointer;
    font-size: 1rem;
    font-weight: bold;
    padding: 10px 1rem;
    letter-spacing: 2px;
    background-color: black;
    color: white;
    position: relative;
    bottom: 3rem;
  }
}